*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: #080710;
}
.background {
  width: 430px;
  height: 520px;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
}
.background .shape {
  height: 200px;
  width: 200px;
  position: absolute;
  border-radius: 50%;
}
.shape:first-child {
  background: linear-gradient(#9b22ea, #bf23f6);
  left: -80px;
  top: -200px;
}
.shape:last-child {
  background: linear-gradient(to right, #ff512f, #f09819);
  bottom: 10px;
  right: -80px;
}
